import { FOLLOW_CLIENT_TO_MODULE, FOLLOW_MODULE_TO_CLIENT } from "../../../kit/events/events";
import AudioManager from "../../../kit/system/audio/AudioManager";
import EventSystem from "../../../kit/system/event/EventSystem";
import RocketProject from "../script/rocketProject";
const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Node)
    lubg1:cc.Node = null;

    @property(cc.Node)
    lubg2:cc.Node = null;

    @property(cc.Node)
    fallstone:cc.Node = null;

    @property(sp.Skeleton)
    rocketAni: sp.Skeleton = null;

    @property(sp.Skeleton)
    fireAni: sp.Skeleton = null;

    @property(cc.Node)
    checkBox:cc.Node[]=[];

    @property(cc.Node)
    boardNode:cc.Node = null;

    @property(cc.Prefab)
    dragItem:cc.Prefab = null;

    @property(cc.Node)
    heartContrl:cc.Node = null;

    @property(cc.Node)
    missileNode:cc.Node = null;

    @property(cc.Node)
    blockNode:cc.Node=null;

    @property(RocketProject)
    rocketProject:RocketProject = null;

    @property(cc.Graphics)
    line2:cc.Graphics = null;

    @property(cc.Graphics)
    line3:cc.Graphics = null;

    @property(cc.Graphics)
    line4:cc.Graphics = null;

    @property(cc.Node)
    naopao:cc.Node=null;

    isRunmap = false;
    cur_lubg:cc.Node = null;
    speed = 200;
    innerId = 0;
    gameWordData:string[]=['黄色'];
    wrongNum:number = 0;
    luheight=750;

    onLoad () {
        this.cur_lubg = this.lubg1;
        EventSystem.on(FOLLOW_MODULE_TO_CLIENT, this.recordResult, this);
    }

    start () {
        let skinName:string = '';
        switch (this.rocketProject.rocketState) {
            case 0:
                skinName = 'buding-0'
                break;
            case 1:
                skinName = 'buding-1'
                break;
            case 2:
                skinName = 'buding-2'
                break;
            case 3:
                skinName = 'buding-3'
                break;
            default:
                break;
        }
        this.rocketAni.setSkin(skinName)

        this.isRunmap= true;
        this.startAni();

        this.setbgfix();
    }

    setRocketState(){
        this.rocketProject.rocketState ++;
        let skinName:string = 'buding-3';
        switch (this.rocketProject.rocketState) {
            case 1:
                skinName = 'buding-1'
                break;
            case 2:
                skinName = 'buding-2'
                break;
            case 3:
                skinName = 'buding-3'
                break;
            default:
                break;
        }
        this.rocketAni.setSkin(skinName)
    }

    setbgfix(){
        let frameSize: cc.Size = cc.view.getFrameSize() // 屏幕尺寸
        let designResolution: cc.Size = cc.Canvas.instance.designResolution
        let designSize: cc.Size = cc.size(designResolution.width, designResolution.height)  // cc.Canvas.instance.designResolution
        let frameAspectRatio: number = frameSize.width / frameSize.height
        let screenSize: cc.Size = { ...designSize } as cc.Size

        let ratio: number = this.node.width / this.node.height
        if (ratio > frameAspectRatio) {
            let width = this.node.width * screenSize.height / this.node.height
            this.lubg1.width = width
            this.lubg1.height = screenSize.height
            this.lubg2.width = width
            this.lubg2.height = screenSize.height
            this.lubg2.y = screenSize.height
            this.luheight = screenSize.height;
        } else {
            let height = this.node.height * screenSize.width / this.node.width
            this.lubg1.width = screenSize.width
            this.lubg1.height = height
            this.lubg2.width = screenSize.width
            this.lubg2.height = height
            this.lubg2.y = height
            this.luheight = height
        }
    }

    startAni(){
        AudioManager.playEffect('rocketProject','audio/rocketfly');
        cc.tween(this.rocketAni.node)
        .to(3,{y:50})
        .delay(2)
        .parallel(
            cc.tween().to(1,{position:cc.v3(300,-200,0)}),
            cc.tween().to(1,{scale:0.4})
        )
        .call(()=>{

            this.playSpine(this.fireAni,'hongse',true)
            this.cueFallStone()
            this.assembleItem()
        })
        .start()
    }

    cueFallStone(){
        cc.tween(this.fallstone)
        .to(0.5,{y:280})
        .start()
    }

    assembleItem(){
        this.boardNode.active = true
        this.boardNode.opacity = 0;
        for (let i = 0; i < 4; i++) {
            let item = cc.instantiate(this.dragItem);
            let itemCom = item.getComponent('dragItem');
            itemCom.initItem(i,this.checkBox,this);
            this.boardNode.addChild(item);  
        }

        cc.tween(this.boardNode)
        .to(0.5,{opacity:255})
        .call(()=>{
            this.naopao.getComponent('naopaoAni').showNaopao(2);
        })
        .start();
    }

    whenDragRight(mid){
        cc.log('drag right==')
        AudioManager.playEffect('rocketProject','audio/game_right');
        this.innerId++;
        if (this.innerId >=4) {
            cc.log('game 3 winning')
            // cc.tween(this.boardNode)
            // .to(0.5,{opacity:0})
            // .start();
            this.missileAni();
            this.rocketProject.setIndicator(2,true);
            return;
        }

        // this.drawLine(mid)
    }

    missileAni(){
        cc.tween(this.fallstone)
        .to(0.5,{x:0})
        .start()

        this.boardNode.active = false;
        this.missileNode.active = true;
        cc.tween(this.missileNode)
        .to(0.5,{x:0})
        .to(0.5,{y:220})
        .call(()=>{
            AudioManager.playEffect('rocketProject','audio/blast');
            this.missileNode.getChildByName('daodan').active = false;
            this.missileNode.getChildByName('blastAni').active = true;
            this.scheduleOnce(()=>{
                this.missileNode.getChildByName('blastAni').active = false;
                cc.tween(this.fallstone)
                .to(0.5,{y:600})
                .call(()=>{
                    //下一个游戏流程
                    this.gamefour()
                })
                .start()
            },1)
        })
        .start()
    }

    missileAniWrong(){
        cc.tween(this.fallstone)
        .to(0.5,{x:0})
        .start()

        this.boardNode.active = false;
        this.missileNode.active = true;
        cc.tween(this.missileNode)
        .to(0.5,{x:0})
        .to(0.5,{y:220})
        .call(()=>{
            cc.tween(this.fallstone)
            .to(0.8,{position:cc.v3(400,600,0)})
            .call(()=>{
                //下一个游戏流程
                this.gamefour();
                this.setRocketState();
            })
            .start()
            this.missileNode.angle = 45;
            cc.tween(this.missileNode)
            .to(0.8,{position:cc.v3(-400,600,0)})
            .start()
        })
        .start()
    }

    whenDragWrong(){
        cc.log('drag wrong==')
        AudioManager.playEffect('rocketProject','audio/game_error');
        let hc = this.heartContrl.childrenCount;
        if (hc > 0) {
            for (let i = 0; i < hc; i++) {
                if (i == hc-1) {
                    this.heartContrl.children[i].destroy();
                    if (hc == 1) {
                        cc.log('game 3 failled')
                        this.setBlockLay(true);
                        this.rocketProject.setIndicator(2,false);
                        this.missileAniWrong();
                    }
                }
            }
        }
    }

    drawLine(tag:number){
        switch (tag) {
            case 0:
                // this.line.moveTo(292, 90);
                // this.line.lineTo(144, 90);
                // this.line.lineTo(144, 0);
                // this.line.stroke();
                break;
            case 1:
                this.line2.moveTo(292, 90);
                this.line2.lineTo(148, 90);
                this.line2.lineTo(148, -24);
                this.line2.stroke();
                break;
            case 2:
                this.line3.moveTo(292, 90);
                this.line3.lineTo(144, 90);
                this.line3.lineTo(144, 0);
                this.line3.stroke();
                break;
            case 3:
                this.line4.moveTo(148, -24);
                this.line4.lineTo(148, -103);
                this.line4.lineTo(-26, -103);
                this.line4.stroke();
                break;
            default:
                break;
        }
    }

    gamefour(){
        cc.tween(this.rocketAni.node)
        .parallel(
            cc.tween().to(1,{position:cc.v3(0,50,0)}),
            cc.tween().to(1,{scale:0.9})
        )
        .call(()=>{
            this.playSpine(this.rocketAni,'daiji',true)
            AudioManager.playEffect('rocketProject','audio/xuli',()=> {
                this.gameRunRecord()
                // AudioManager.playEffect('rocketProject','audio/yel');
                // EventSystem.emit(FOLLOW_CLIENT_TO_MODULE, { eventName: "display"});
                //this.scheduleOnce(()=>this.gameRunRecord(),2);
            });
            // this.scheduleOnce(()=>{
            //     AudioManager.playEffect('rocketProject','audio/yel');
            //     this.scheduleOnce(()=>this.gameRunRecord(),2);
            // },4)
            this.rocketAni.node.getChildByName('huiju').active = true;
        })
        .start()
    }

    /** 运行跟读环节 */
  private gameRunRecord() {
    cc.log("gameRunRecord~~~");
    // let id = cc.audioEngine.play(this.gameWordAudio[this.gameIndex], false, 1.0);
    let recordData = {
        evaluatingType: 1,
        evaluatingText: this.gameWordData[0],
        tryTimes: 2,
        evaluationScore: 30,
        recordTime: 3,
      };

      EventSystem.emit(FOLLOW_CLIENT_TO_MODULE, {
        eventName: "initComponent",
        extra: recordData,
      }); //初始化录音
      let startRecordData ={
          evaluatingText:this.gameWordData[0],
          bundleName:'rocketProject',
          audioUrl:'audio/yel',
          iconPath: "res/yellow"
      }
      cc.log("gameTwp:", startRecordData);
      EventSystem.emit(FOLLOW_CLIENT_TO_MODULE, { eventName: "startRecord",extra:startRecordData }); //开始录音

      // if (this.isTest || CC_DEBUG) {
        // this.scheduleOnce(() => {
        //   let num = Math.random();
        //   this.sayRight();
        // }, 3);
      // }
  }

   /** 检测返回来的结果 */
   private recordResult(result) {
    console.log("recordResult" + result.data);
    if (result.data.eventName == "ResultCb") {
        let score = result.data.extra.score;
        if (result.data.success) {
            this.sayRight()
        } else {
            this.sayWrong()
        }
    }
  }

    sayRight(){
        this.playSpine(this.fireAni,'lanse',true)
        this.rocketAni.node.getChildByName('huiju').active = false;
        this.gameEnd(true);
    }

    sayWrong(){
        this.setRocketState();
        this.rocketAni.node.getChildByName('huiju').active = false;
        this.rocketAni.node.getChildByName('penyan').active = true;
        // this.
        cc.tween(this.rocketAni.node)
        .by(0.1,{x:25}, { easing: 'quartIn'})
        .by(0.1,{x:-25}, { easing: 'quartOut'})
        .by(0.1,{x:-25}, { easing: 'quartIn'})
        .by(0.1,{x:25}, { easing: 'quartOut'})
        .union()
        .repeat(4)
        .call(()=>{
            this.rocketAni.node.getChildByName('penyan').active = false;
            this.gameEnd(false)
        })
        .start()
    }

    gameEnd(tag:boolean){
        EventSystem.off(FOLLOW_MODULE_TO_CLIENT, this.recordResult, this);
        this.rocketProject.setIndicator(3,tag);
        //游戏结束
        this.playSpine(this.rocketAni,'fei',true)
        AudioManager.playEffect('rocketProject','audio/rocketfly');
        cc.tween(this.rocketAni.node)
        .by(4,{y:900})
        .call(()=>{
            this.rocketProject.gameTowOver();
        })
        .start()
    }

    runlubg(dt){
        var s = dt * this.speed;
        this.lubg1.y -= s;
        this.lubg2.y -= s;
        if (this.cur_lubg.y <= -this.luheight) { // 地图切换
            if(this.cur_lubg == this.lubg2) {
                this.lubg2.y = this.lubg1.y + this.luheight;
                this.cur_lubg = this.lubg1;
            }
            else {
                this.lubg1.y = this.lubg2.y + this.luheight;
                this.cur_lubg = this.lubg2;
            }
        }
    }
    
    update (dt) {
        if (this.isRunmap) {
            this.runlubg(dt);
        }
    }

    setBlockLay(tag:boolean){
        this.blockNode.active = tag;
    }

    playSpine(spNode,spName,tg) {
        spNode.clearTracks();
        spNode.setToSetupPose();
        spNode.setAnimation(0, spName, tg);
    }
}
