
const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {
    @property(sp.Skeleton)
    naopao:sp.Skeleton=null;

    @property(sp.Skeleton)
    hand:sp.Skeleton=null;

    @property(cc.Node)
    content:cc.Node = null;

    @property(cc.Sprite)
    itemSp:cc.Sprite = null;

    @property(cc.SpriteFrame)
    itemSpArr:cc.SpriteFrame[]=[];

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {
        this.naopao.setCompleteListener(this.naopaoAniback.bind(this));
    }

    showNaopao(tid:number){
        this.node.active = true;
        this.itemSp.spriteFrame = this.itemSpArr[tid];
        this.playSpine(this.naopao,"naopao-1",false);
        this.playSpine(this.hand,"dianji",false);

        if(tid == 2){
            this.hand.node.x -= 80;
            this.itemSp.node.x -= 80;
            this.node.getChildByName('content').getChildByName('board').active = true;

            cc.tween(this.hand.node)
            .delay(0.8)
            .by(0.5,{x:163})
            .start()
            cc.tween(this.itemSp.node)
            .delay(0.8)
            .by(0.5,{x:163})
            .start()
        }

        this.scheduleOnce(()=>{
            this.content.active = false;
            this.node.active = false;
        },2)
    }

    naopaoAniback(date){
        let aniDate = date;
        if (aniDate.animation.name === "naopao-1") {
            this.content.active = true;
        }
    }

    playSpine(spNode,spName,tg) {
        spNode.clearTracks();
        spNode.setToSetupPose();
        spNode.setAnimation(0, spName, tg);
    }
    // update (dt) {}
}
