
import AudioManager from "../../../kit/system/audio/AudioManager";
import GameOne from "../script/gameOne";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(sp.Skeleton)
    btnAni: sp.Skeleton = null;

    @property(cc.Button)
    mbtn:cc.Button=null;

    @property(cc.Node)
    lgNode:cc.Node = null;

    skinArr = ['lanse','huangse','hongse']
    posiArr = [-500,0,500];
    mainCom:GameOne = null;
    mid:number = 0;
    mtag:boolean = false;

    onLoad () {
        this.btnAni.setCompleteListener(this.btnAniback.bind(this));
    }

    initItem(id,tag,mc){
        this.mid = id;
        this.mtag = tag;
        this.mainCom = mc;
    }

    start () {
        this.btnAni.setSkin(this.skinArr[this.mid]);
        this.node.x = this.posiArr[this.mid];
        this.node.y += 50;
    }

    setLg(tag:boolean=false){
        // this.lgNode.active = tag;
        cc.tween(this.node)
        .by(0.2,{scale:0.1})
        .by(0.2,{scale:-0.1})
        .union()
        .repeat(2)
        .start()
    }

    onbtnBack(){
        cc.log('contrbtn clicked =====',this.mtag)
        // this.mbut.interactable = false;
        AudioManager.playEffect('rocketProject','audio/batteryClick');
        this.playSpine(this.btnAni,'anniu',false);
        
        this.mainCom.onContrBtnBack(this.mid,this.mtag);
    }

    btnAniback(){
        if (this.mtag) {
            //'太棒了'音效
            AudioManager.playEffect('rocketProject','audio/game_right');
        } else {
            AudioManager.playEffect('rocketProject','audio/game_error');
        }
        this.mainCom.btnAniBack(this.mid,this.mtag);
    }

    playSpine(spNode,spName,tg) {
        spNode.clearTracks();
        spNode.setToSetupPose();
        spNode.setAnimation(0, spName, tg);
    }

    // update (dt) {}
}
