
import AudioManager from "../../../kit/system/audio/AudioManager";
import GameOne from "../script/gameOne";
const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Sprite)
    showSp:cc.Sprite = null;

    @property(cc.SpriteFrame)
    showSpArr:cc.SpriteFrame[]=[];

    @property(cc.Node)
    lgNode:cc.Node = null;

    mainCom:GameOne = null;
    // colorArr = ["#FFD400","#0000FF","#d71345"]
    posiArr = [-250,0,250];
    timeArr = [3,2,1];
    mid:number = 0;
    mtag:boolean = false;
    // onLoad () {}

    initBattery(id:number,tag:boolean=false,mc:GameOne){
        this.mid = id;
        this.mtag = tag;
        this.mainCom = mc;
    }

    start () {
        // let col:string = this.colorArr[this.mid];
        // this.showSp.node.color = new cc.Color().fromHEX(col);
        this.showSp.spriteFrame = this.showSpArr[this.mid];
        this.node.x = 500;
        this.node.y += 2;

        this.moveBattery(this.timeArr[this.mid],this.posiArr[this.mid]);
    }

    moveBattery(mtime:number,posi:number){
        cc.tween(this.node)
        .to(mtime,{x:posi})
        .call(()=>{
            this.showSp.node.active = true;
        })
        .start()
    }

    setLg(tag:boolean=false){
        // this.lgNode.active = tag;
        cc.tween(this.node)
        .by(0.2,{scale:0.1})
        .by(0.2,{scale:-0.1})
        .union()
        .repeat(2)
        .start()
    }

    onbutClicked(){
        cc.log('battery clicked =====',this.mtag)
        AudioManager.playEffect('rocketProject','audio/batteryClick');
        // this.mbut.interactable = false;
        this.flyBatteryToRocket();
        this.mainCom.onItemClicked(this.mid,this.mtag);
    }

    flyBatteryToRocket(){
        AudioManager.playEffect('rocketProject','audio/batteryfly');
        cc.tween(this.node)
        .parallel(
            cc.tween().to(0.7,{position:cc.v3(-40,300,0)}),
            cc.tween().to(1,{scale:0})
        )
        .call(()=>{
            if (this.mtag) {
                //'太棒了'音效
                AudioManager.playEffect('rocketProject','audio/game_right');
            } else {
                AudioManager.playEffect('rocketProject','audio/game_error');
            }
            this.scheduleOnce(()=>this.mainCom.batteryMoveAniBack(this.mid,this.mtag),1);
            
        })
        .start()
        
    }

    // update (dt) {}
}
