import { LIFE_CYCLE_BLOCK, LIFE_CYCLE_COMPLETE, LIFE_CYCLE_CREATED, LIFE_CYCLE_UNBLOCK } from "../../../kit/events/events";
import AudioManager from "../../../kit/system/audio/AudioManager";
import EventSystem from "../../../kit/system/event/EventSystem";


const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {
    

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}
    @property(cc.Node)
    gameOne:cc.Node = null;

    @property(cc.Node)
    gameTwo:cc.Node = null;

    @property(cc.Node)
    indicatorArr:cc.Node[]=[];

    @property(cc.Node)
    indicatorNode:cc.Node = null;

    informOneTag:boolean = true;
    rocketState:number = 0;

    start () {
        EventSystem.emit(LIFE_CYCLE_CREATED);
        EventSystem.on(LIFE_CYCLE_BLOCK,this.onBlock,this);
        EventSystem.on(LIFE_CYCLE_UNBLOCK,this.unBlock,this);

        AudioManager.playMusic("rocketProject", "audio/BGM_QUIZ01HuoJian1")
    }

    onDestroy(){
        EventSystem.off(LIFE_CYCLE_BLOCK,this.onBlock,this);
        EventSystem.off(LIFE_CYCLE_UNBLOCK,this.unBlock,this);
    }

    gameOneOver(){
        this.gameOne.active = false;
        this.gameTwo.active = true;
    }

    gameTowOver(){
        cc.log('通知容器 游戏结束')
        if (this.informOneTag) {
            this.informOneTag = false;
            EventSystem.emit(LIFE_CYCLE_COMPLETE);
        }
        
    }

    onBlock(){
        console.log('game blocked')
        cc.audioEngine.pauseMusic();
        cc.audioEngine.pauseAllEffects();
        // cc.game.pause();
    }

    unBlock(){
        console.log('game unblocked')
        cc.audioEngine.resumeMusic();
        cc.audioEngine.resumeAllEffects();
        // cc.game.resume();
    }

    setIndicator(mid:number,tag:boolean){
        if(this.indicatorArr){
            cc.log('this.indicatorArr ===',this.indicatorArr.length)
            let intor = this.indicatorArr[mid];
            if (tag) {
                intor.getChildByName('green').active = true
            } else {
                intor.getChildByName('red').active = true
            }
            switch (mid) {
                case 0:
                    this.indicatorNode.getChildByName('line1').active = true;
                    break;
                case 1:
                    this.indicatorNode.getChildByName('line2').active = true;
                    break;
                case 2:
                    this.indicatorNode.getChildByName('line3').active = true;
                    break;
                default:
                    break;
            }
        } 
    }

    // update (dt) {}
}
