import AudioManager from "../../../kit/system/audio/AudioManager";
import RocketProject from "../script/rocketProject";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(sp.Skeleton)
    rocketAni: sp.Skeleton = null;

    @property(sp.Skeleton)
    conveyorAni: sp.Skeleton = null;

    @property(cc.Prefab)
    batteryItem:cc.Prefab = null;

    @property(cc.Node)
    boardNode:cc.Node=null;

    @property(cc.Prefab)
    btnItem:cc.Prefab = null;

    @property(cc.Node)
    controlNode:cc.Node=null;

    @property(cc.Node)
    blockNode:cc.Node=null;

    @property(RocketProject)
    rocketProject:RocketProject = null;

    @property(cc.Node)
    naopao:cc.Node=null;

    answer_arr = [0,1];
    sounds_arr = ['duck','cow','goat','It_is_a_cow','It_is_a_goat','It_is_a_duck'];
    cur_index = 0;
    sayhandle = null;

    onLoad () {
        // this.duckAni.setCompleteListener(this.duckAniback.bind(this));
    }

    start () {
        this.scheduleOnce(()=>{
            AudioManager.playEffect('rocketProject','audio/conveyor');
            this.playSpine(this.conveyorAni,'1',true);
        },2);
        this.scheduleOnce(()=>this.cueBatteryFun(),1);
    }

    cueBatteryFun(){
        let itemId = 0;
        this.schedule(()=>{
            if (itemId < 3) {
                this.addBatteryToBoard(itemId);
                itemId++; 
            }else{
                this.conveyorAni.paused = true;
                AudioManager.playEffect('rocketProject','audio/yel',()=>{
                    this.setBatterLg();
                    this.naopao.getComponent('naopaoAni').showNaopao(0);
                });
                this.schedule(this.sayhandle=()=>{
                    AudioManager.playEffect('rocketProject','audio/yel');
                    this.setBatterLg();
                },5)
                this.scheduleOnce(()=>this.setBlockLay(false),1);
            }
        },1,3)  
    }


    addBatteryToBoard(itemId){
        let antag = itemId == this.answer_arr[this.cur_index];
        let item = this.getOneBatteryItem(itemId,antag);
        item.opacity = 0;
        this.boardNode.addChild(item)

        cc.tween(item)
        .to(0.5,{opacity:255})
        .start()
    }

    setBatterLg(tag:boolean=false,id:number=-1){
        let count = this.boardNode.childrenCount;
        for (let i = 0; i < count; i++) {
            if (i != id) {
                let itemcom = this.boardNode.children[i].getComponent("battery")
                itemcom.setLg(tag);
            }
        }
    }

    getOneBatteryItem(i,tag:boolean = false):cc.Node{
        let item = cc.instantiate(this.batteryItem);
        let itemCom = item.getComponent('battery');
        itemCom.initBattery(i,tag,this);
        return item;
    }

    onItemClicked(mid:number,mtag:boolean){
        // this.setBatterLg(false,mid);
        this.setBlockLay(true);
        this.unschedule(this.sayhandle);
    }

    setRocketState(){
        this.rocketProject.rocketState ++;
        let skinName:string = 'buding-3';
        switch (this.rocketProject.rocketState) {
            case 0:
                skinName = 'buding-0'
                break;
            case 1:
                skinName = 'buding-1'
                break;
            case 2:
                skinName = 'buding-2'
                break;
            case 3:
                skinName = 'buding-3'
                break;
            default:
                break;
        }
        this.rocketAni.setSkin(skinName)
    }

    batteryMoveAniBack(mid:number,mtag:boolean){
        if (mtag) {//还要处理指示器和火箭损坏
            AudioManager.playEffect('rocketProject','audio/rocketRight');
            this.playSpine(this.rocketAni,'xuli',true)//火箭蓄力待飞
            this.scheduleOnce(()=>this.nextStep(),2);
        } else {
            this.setRocketState();
            AudioManager.playEffect('rocketProject','audio/rocketWrong');
            //火箭失败特效后 蓄力待飞
            this.failedEff();
            this.scheduleOnce(()=>this.nextStep(),4);
        }

        let ccout = this.boardNode.childrenCount;
        for (let i = 0; i < ccout; i++) {
            this.boardNode.children[i].destroy();
        }
        this.rocketProject.setIndicator(this.cur_index,mtag);
        
    }

    failedEff(){
        let dian = this.rocketAni.node.getChildByName("dian");
        let yan = this.rocketAni.node.getChildByName("yan");
        dian.active = true;
        this.scheduleOnce(()=>{
            dian.active = false;
            yan.active = true;
            this.scheduleOnce(()=>{
                yan.active = false;
                this.playSpine(this.rocketAni,'xuli',true)//火箭蓄力待飞
            },0.7)
        },2)
    }

    nextStep(){
        this.cur_index++;
        this.showControlBoardAni()
    }

    showControlBoardAni(){
        this.controlNode.active = true;
        this.assembleBtnBoard(3);
        AudioManager.playEffect('rocketProject','audio/contrboard');
        cc.tween(this.controlNode)
        .by(1,{y:260})
        .call(()=>{
            AudioManager.playEffect('rocketProject','audio/yel',()=>{
                this.naopao.getComponent('naopaoAni').showNaopao(1);
                this.setBtnLg();
            });
            this.schedule(this.sayhandle=()=>{
                AudioManager.playEffect('rocketProject','audio/yel');
                this.setBtnLg();
            },5)
            this.scheduleOnce(()=>this.setBlockLay(false),1);
        })
        .start()
    }

    assembleBtnBoard(ctag){
        this.controlNode.destroyAllChildren();
        for (let i = 0; i < ctag; i++) {
            let item = cc.instantiate(this.btnItem);
            let itemCom = item.getComponent('btnItem');
            let antag = i == this.answer_arr[this.cur_index];
            itemCom.initItem(i,antag,this);
            this.controlNode.addChild(item);  
        }
    }

    setBtnLg(tag:boolean=false,id:number=-1){
        let count = this.controlNode.childrenCount;
        for (let i = 0; i < count; i++) {
            if (i != id) {
                let itemcom = this.controlNode.children[i].getComponent("btnItem")
                itemcom.setLg(tag);
            }
        }
    }

    onContrBtnBack(mid:number,mtag:boolean){
        // this.setBtnLg(false,mid);
        this.setBlockLay(true);
        this.unschedule(this.sayhandle);
    }

    btnAniBack(mid:number,mtag:boolean){
        this.rocketProject.setIndicator(this.cur_index,mtag);
        AudioManager.playEffect('rocketProject','audio/contrboard');
        cc.tween(this.controlNode)
        .by(1,{y:-260})
        .call(()=>{
            this.controlNode.active = false;
            if (mtag) {
                this.flyTheRocket()
            } else {
                this.setRocketState();
                this.rocketShakeFun()
            }
            
        })
        .start()
    }

    rocketShakeFun(){
        let dian = this.rocketAni.node.getChildByName("dian");
        dian.active = true;
        cc.tween(this.rocketAni.node)
        .by(0.1,{x:25}, { easing: 'quartIn'})
        .by(0.1,{x:-25}, { easing: 'quartOut'})
        .by(0.1,{x:-25}, { easing: 'quartIn'})
        .by(0.1,{x:25}, { easing: 'quartOut'})
        .union()
        .repeat(4)
        .call(()=>{
            dian.active = false;
            this.flyTheRocket()//晃动之后升空火箭
        })
        .start()
    }

    flyTheRocket(){
        this.rocketAni.node.getChildByName('fire').active = true;
        this.playSpine(this.rocketAni,'fei',true)
        AudioManager.playEffect('rocketProject','audio/rocketfly');
        cc.tween(this.rocketAni.node)
        .to(4,{y:650}, { easing: 'quartIn'})
        .call(()=>{
            //准备进入下一个游戏场景
            this.rocketProject.gameOneOver();
        })
        .start()
    }

    setBlockLay(tag:boolean){
        this.blockNode.active = tag;
    }

    playSpine(spNode,spName,tg) {
        spNode.clearTracks();
        spNode.setToSetupPose();
        spNode.setAnimation(0, spName, tg);
    }

    // update (dt) {}
}
