import GameTwo from "../script/gameTwo";
const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.SpriteFrame)
    cardSpArr:cc.SpriteFrame[]=[];

    @property(cc.Sprite)
    cardSp:cc.Sprite = null;

    checkBox:cc.Node[]=[];
    mainCom:GameTwo = null;
    isCanMove = true;
    mid = -1;
    mround = 0;
    checkId = -1;
    xoffset:number = -350;
    PosiArr = [cc.v3(this.xoffset,226,0),cc.v3(this.xoffset,91,0),cc.v3(this.xoffset,-40,0),cc.v3(this.xoffset,-174,0)]
    ckidArr = [[0,1,2,3],[1,0,2,3],[1,2,3,0],[0,3,1,2]];

    onLoad () {
        this.node.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.on(cc.Node.EventType.TOUCH_START, this.touchStart, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this.touchEnd, this); 
        this.node.on(cc.Node.EventType.TOUCH_CANCEL, this.touchEnd, this);
    }

    onDestroy(){
        this.node.off(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.off(cc.Node.EventType.TOUCH_START, this.touchStart, this);
        this.node.off(cc.Node.EventType.TOUCH_END, this.touchEnd, this);
        this.node.off(cc.Node.EventType.TOUCH_CANCEL, this.touchEnd, this);
    }

    initItem(id,cb,mc){
        this.mid = id;
        this.checkBox = cb;
        this.mainCom = mc;
    }

    backPosiAni(){
        cc.tween(this.node)
        .to(0.2,{position:this.PosiArr[this.mid]})
        .start();
    }

    reSetCheckId(round){
        this.mround = round;
        this.checkId = this.ckidArr[this.mround][this.mid];
    }

    start () {
        this.cardSp.spriteFrame = this.cardSpArr[this.mid];
        this.node.position = this.PosiArr[this.mid];
        this.checkId = this.ckidArr[this.mround][this.mid];
    }

    // onCollisionEnter(other, self) {
        // console.log('on collision enter');
        // other.node.destroy();
        // this.isCanMove = false;
    // }
    touchStart(){
        if (!this.isCanMove) {
            return;
        }
        this.node.zIndex = 9;
        this.node.scale = 1.4;
    }

    touchEnd(){
        if (!this.isCanMove) {
            return;
        }
        this.setCanTouch(false)
        this.node.zIndex = 0;

        let myv2 = cc.v2(this.node.position.x,this.node.position.y);
        let checkRect= this.checkBox[this.checkId].getBoundingBox();
        if (checkRect.contains(myv2)) {
            this.node.position = this.checkBox[this.checkId].position;
            this.checkBox[this.checkId].color = cc.color(255,255,255);
            this.node.scale = 1.8;
            this.mainCom.whenDragRight(this.mid);
        }else{
            this.node.scale = 1;
            for (let i = 0; i < this.checkBox.length; i++) {
                if (i!=this.checkId) {
                    let checkRect= this.checkBox[i].getBoundingBox();
                    if (checkRect.contains(myv2)) {
                        this.mainCom.whenDragWrong();
                    }
                } 
            }
            
            // this.scheduleOnce(()=>{
            //     this.node.position = this.PosiArr[this.mid];
            // },0.5)
            cc.tween(this.node)
            .to(0.2,{position:this.PosiArr[this.mid]})
            .start()
            this.scheduleOnce(()=>{
                this.setCanTouch(true)
            },0.5)
            
        }

        
    }

    onTouchMove(touchEvent){
        if (!this.isCanMove) {
            return;
        }
        // cc.log('move')
        //通过touchEvent获取当前触摸坐标点
        let location = touchEvent.getLocation();
        // this.curLoc = location;
        //修改节点位置，注意要使用父节点进行对触摸点进行坐标转换
        let bendiLoc = this.node.parent.convertToNodeSpaceAR(location);
        if (bendiLoc.x> -400 && bendiLoc.x <400) {
            this.node.x = bendiLoc.x;
        }
        if (bendiLoc.y> -300 && bendiLoc.y <300) {
            this.node.y = bendiLoc.y;
        }

        let myv2 = cc.v2(this.node.position.x,this.node.position.y);
        let checkRect= this.checkBox[this.checkId].getBoundingBox();
        
        if (checkRect.contains(myv2)) {
            cc.log('into board===')
            this.checkBox[this.checkId].color = cc.color(80,80,80);
        }else{
            this.checkBox[this.checkId].color = cc.color(255,255,255);
        }
    }

    setCanTouch(tag:boolean){
        this.isCanMove = tag;
    }

    // update (dt) {}
}
